*{
  font-family: 'Roboto Slab', serif;
}

header{
  height: 8vh;
  width:100%;
  background-image: -webkit-gradient(linear,right top, left top,from(#B4106C),to(#E63976));
  background-image: -webkit-linear-gradient(right,#B4106C,#E63976);
  background-image: -o-linear-gradient(right,#B4106C,#E63976);
  background-image: linear-gradient(to left,#B4106C,#E63976);
}

#title-nav{
  height: 6vh;
  width:100%;
  background:#B4106C;
  text-align: center;
  color:#fff;
  font-size: 1.5em;
}

#nav-sections{
  height: 6vh;
  width:100%;
  background:#BF2C62;
  color:#fff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

#nav-sections div{
  width:8%;
  text-align: center;
}

#category-cover{
  height: 37vh;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0,0.4)), to(rgba(0, 0, 0,0.4))), url("https://canastarosa.s3.amazonaws.com/media/__sized__/market/category/5186a3d3df3f36c137b5c61730a9c8ad-crop-c0-5__0-5-1280x268.png");
  background-image: -webkit-linear-gradient(rgba(0, 0, 0,0.4), rgba(0, 0, 0,0.4)), url("https://canastarosa.s3.amazonaws.com/media/__sized__/market/category/5186a3d3df3f36c137b5c61730a9c8ad-crop-c0-5__0-5-1280x268.png");
  background-image: -o-linear-gradient(rgba(0, 0, 0,0.4), rgba(0, 0, 0,0.4)), url("https://canastarosa.s3.amazonaws.com/media/__sized__/market/category/5186a3d3df3f36c137b5c61730a9c8ad-crop-c0-5__0-5-1280x268.png");
  background-image: linear-gradient(rgba(0, 0, 0,0.4), rgba(0, 0, 0,0.4)), url("https://canastarosa.s3.amazonaws.com/media/__sized__/market/category/5186a3d3df3f36c137b5c61730a9c8ad-crop-c0-5__0-5-1280x268.png");
}

#category-cover{
  color:#fff;
  font-size: 2em;
}

section{
  display: -ms-flexbox;
  display: flex;
}

#stores{
  width: 30%;
  padding-left: 6%;
}

#sub-nav{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  width:100%;
}

#title-count,
#sub-nav form,
#order-prods{
  width:33%;
  height:8vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

#results{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  width:100%;
  
}

.prod-card{
  width:25%;
}

.img-box{
  height:25vh;
  width:100%;
}

img{
  height:100%;
}



